<template>
  <div id="pub-wrapper">
    <div id="good-pub">
      <h1 id="title">广告发布</h1>
      <p id="describe">{{desc}}</p>
      <div id="goods-type">
        <span>有效版本：</span>
        <v-mulselect :goodType="goodType"></v-mulselect>
      </div>
      <div id="area">
        <span>适用地区：</span>
        <select>
          <option v-for="(item,index) in area" :value="item.value">{{item.label}}</option>
        </select>
      </div>
      <div id="use-time">
        <span>有效时间：</span>
        <i>半年</i>
      </div>
      <div id="price">
        <span>总价：</span>
        <i>500元</i>
      </div>
      <button id="buy-btn">立即购买</button>
    </div>
    <div id="good-desc">
      <h1>产品说明</h1>
      <p id="desc">{{desc}}</p>
      <table id="goods-table" cellpadding="0" cellspacing="0">
        <tr id="tr" v-for="(item,index) in goods">
          <td id="td" v-for="(goods,index) in item">{{goods}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
  import mulSelect from '../../components/mulselect'
  export default {
    data(){
      return{
        selected:false,
        nowindex:0,
        goodType:[
          {
            label:'客户版',
            value:0,
            select:false
          },
          {
            label:'代理商版',
            value:1,
            select:false
          },
          {
            label:'专家版',
            value:2,
            select:false
          }
        ],
        desc:'广告活动按照广告计划执行，到完成广告创作' +
          '并形成广告作品之后，经过广告主的最后审核同意，' +
          '即可送到预定的媒介发布刊播。这项工作一般由媒介部门' +
          '的有关专业人员负责，他们的任务就是专门负责与有关媒介单' +
          '位接洽，安排有关广告的发播事宜，并对发播质量实施监督。',
        area:[
          {
            label: '北京',
            value: 0
          },
          {
            label: '上海',
            value: 1
          },
          {
            label: '广州',
            value: 2
          },
          {
            label: '天津',
            value: 3
          },
          {
            label: '武汉',
            value: 4
          },
          {
            label: '重庆',
            value: 5
          },
        ],
        goods:[
          ['安全安保','办公文教','彩票','车辆物流','成人用品','出版传媒','电脑硬件'],
          ['电子电工','房地产建筑装修','分类平台','服装鞋帽','箱包饰品','化工原料制品','机械设备'],
          ['安全安保','办公文教','彩票','车辆物流','成人用品','出版传媒','电脑硬件'],
          ['安全安保','办公文教','彩票','车辆物流','成人用品','出版传媒','电脑硬件'],
          ['安全安保','办公文教','彩票','车辆物流','成人用品','出版传媒','电脑硬件']
        ]
      }
    },
    components:{
      'v-mulselect':mulSelect,
    },
    methods:{
      select(index){
        this.nowindex=index;
      }
    }
  }
</script>

<style lang="stylus" scoped>
  #good-pub
    margin-bottom 30px
    background #fff
    padding 20px
    #title
      font-size 20px
    #describe
      font-size 14px
      color gray
      padding 10px 0
    #goods-type
      display flex
      line-height 25px
      padding 10px 0
      >span
        width 100px
        display inline-block
    #area
      line-height 25px
      padding 10px 0
      >span
        width 95px
        display inline-block
      >select
        padding 5px 0
        padding-top 1px
        border-radius 5px
        font-size 14px
        >option
          padding 0
    #use-time
      line-height 25px
      padding 10px 0
      >span
        width 100px
        display inline-block
    #price
      line-height 25px
      padding 10px 0
      >span
        width 100px
        display inline-block
      >i
        color red
    #buy-btn
      margin-left 100px
      margin-top 10px
      padding 10px
      background #4fc08d
      color #fff
      border-radius 5px
      cursor pointer
  #good-desc
    background #fff
    padding 20px
    >h1
      font-size 20px
    #desc
      font-size 14px
      color gray
      padding 15px 0
    #goods-table
      border-bottom 1px solid #dcdcdc
      border-right 1px solid #dcdcdc
      width 100%
      #td
        font-size 16px
        border 1px solid #dcdcdc
        border-bottom 0
        border-right 0
        line-height 50px
        padding 0 15px
        color #4c4c4c
</style>



