<template>
  <div id="count-wrapper">
    <div id="good-count">
      <h1 id="title">数据统计</h1>
      <p id="describe">{{desc}}</p>
      <div id="goods-type">
        <span>产品类型：</span>
        <v-singleselect :goodType="goodType"></v-singleselect>
      </div>
      <div id="area">
        <span>适用地区：</span>
        <v-option :optionList="area"></v-option>
      </div>
      <div id="use-time">
        <span>有效时间：</span>
        <i>半年</i>
      </div>
      <div id="price">
        <span>总价：</span>
        <i>500元</i>
      </div>
      <button id="buy-btn">立即购买</button>
    </div>
    <div id="good-desc">
      <h1>产品说明</h1>
      <p id="desc">{{desc}}</p>
      <table id="goods-table" cellpadding="0" cellspacing="0">
        <tr id="tr" v-for="(item,index) in goods">
          <td id="td" v-for="(goods,index) in item">{{goods}}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
  import singleSelect from '../../components/singleselect'
  import option from '../../components/option'
  export default {
    data(){
      return{
        selected:false,
        nowindex:0,
        goodType:[
          {
            label:'红色版',
            value:0
          },
          {
            label:'绿色版',
            value:1
          },
          {
            label:'紫色版',
            value:2
          },
        ],
        desc:'历史资料、科学实验、检验、统计等所获得的和用于科学研究、技术设计、查证、决策等的数值加以统计为解决方案做前期准备。',
        area:[
          {
            label: '北京',
            value: 0
          },
          {
            label: '上海',
            value: 1
          },
          {
            label: '广州',
            value: 2
          },
          {
            label: '天津',
            value: 3
          },
          {
            label: '武汉',
            value: 4
          },
          {
            label: '重庆',
            value: 5
          },
        ],
        goods:[
          ['安全安保','办公文教','彩票','车辆物流','成人用品','出版传媒','电脑硬件'],
          ['电子电工','房地产建筑装修','分类平台','服装鞋帽','箱包饰品','化工原料制品','机械设备'],
          ['安全安保','办公文教','彩票','车辆物流','成人用品','出版传媒','电脑硬件'],
          ['安全安保','办公文教','彩票','车辆物流','成人用品','出版传媒','电脑硬件'],
          ['安全安保','办公文教','彩票','车辆物流','成人用品','出版传媒','电脑硬件']
        ]
      }
    },
    components:{
      'v-singleselect':singleSelect,
      'v-option':option
    }
  }
</script>

<style lang="stylus" scoped>
  #good-count
    margin-bottom 30px
    background #fff
    padding 20px
    #title
      font-size 20px
    #describe
      font-size 14px
      color gray
      padding 10px 0
    #goods-type
      display flex
      line-height 25px
      padding 10px 0
      >span
        width 100px
        display inline-block
    #area
      line-height 25px
      padding 10px 0
      >span
        width 95px
        display inline-block
    #use-time
      line-height 25px
      padding 10px 0
      >span
        width 100px
        display inline-block
    #price
      line-height 25px
      padding 10px 0
      >span
        width 100px
        display inline-block
      >i
        color red
    #buy-btn
      margin-left 100px
      margin-top 10px
      padding 10px
      background #4fc08d
      color #fff
      border-radius 5px
      cursor pointer
  #good-desc
    background #fff
    padding 20px
    >h1
      font-size 20px
    #desc
      font-size 14px
      color gray
      padding 15px 0
    #goods-table
      border-bottom 1px solid #dcdcdc
      border-right 1px solid #dcdcdc
      width 100%
      #td
        font-size 16px
        border 1px solid #dcdcdc
        border-bottom 0
        border-right 0
        line-height 50px
        padding 0 15px
        color #4c4c4c
</style>



