<template>
    <div id="detail-wrapper">
      <div id="left-flow">
        <div id="goods-img">
          <img :src="goodimg" alt="">
        </div>
        <ul id="good-detail">
          <router-link tag="li" id="detail-list"
                       v-for="(item,index) in products" :key="index"
                       :to="item.path"
                        active-class="select">
            {{item.name}}
          </router-link>
        </ul>
      </div>
      <div id="right-flow">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </div>
    </div>
</template>

<script>
    export default {
        data(){
          return{
            products: [
              {
                name: '数据统计',
                path: 'count',
                icon: require('../assets/images/1.png'),
                active: false
              },
              {
                name: '数据预测',
                path: 'forecast',
                active: false
              },
              {
                name: '流量分析',
                path: 'analysis',
                active: false
              },
              {
                name: '广告发布',
                path: 'publish',
                active: false
              }
            ],
            imgMap: {
              '/detail/count': require("../assets/images/1.png"),
              '/detail/forecast': require("../assets/images/2.png"),
              '/detail/analysis': require("../assets/images/3.png"),
              '/detail/publish': require("../assets/images/4.png")
            }
          }
        },
        computed:{
          goodimg(){
            return this.imgMap[this.$route.path];
          }
        }
    }
</script>

<style lang="stylus" scoped>
  #detail-wrapper
    width 80%
    margin auto
    display flex
    justify-content space-between
    padding 30px 0
    #left-flow
      flex-basis 200px
      display flex
      justify-content center
      padding 20px 0px
      box-sizing border-box
      background #fff
      flex-flow column
      height 290px
      #goods-img
        text-align center
        img
          width 100px
          margin-bottom 10px
      #detail-list
        line-height 35px
        background #fff
        color #000
        text-align center
        cursor pointer
        &:hover
          background #4fc08d
          color #fff
        &.select
          background #4fc08d
          color #fff
    #right-flow
      width 85%
</style>



