<template>
  <div id="app">
    <v-header @log-show="showlog" @reg-show="showreg" @about-show="showabout"></v-header>
    <div id="container">
      <keep-alive>
        <router-view/>
      </keep-alive>
    </div>
    <v-foot></v-foot>
    <v-diglog v-show="logshow" @pop-close="closepop">
      <v-logform @pop-close="popclose" @has-log="attrName"></v-logform>
    </v-diglog>
    <v-diglog v-show="regshow" @pop-close="closepop">
      <v-regform></v-regform>
    </v-diglog>
    <v-diglog v-show="aboutshow" @pop-close="closepop">
      <v-aboutform></v-aboutform>
    </v-diglog>
  </div>
</template>

<script>
  import Header from './components/header'
  import Foot from './components/footer'
  import Diglog from './components/diglog'
  import Logform from './components/form/logForm'
  import Regform from './components/form/regForm'
  import Aboutform from './components/form/about'
export default {
  data(){
    return{
      logshow:false,
      regshow:false,
      aboutshow:false,
    }
  },
  methods:{
    closepop(val){
      this.logshow=val;
      this.regshow=val;
      this.aboutshow=val;
    },
    showlog(val){
      console.log(val);
      this.logshow=val;
    },
    showreg(val){
      this.regshow=val;
    },
    showabout(val){
      this.aboutshow=val;
    },
    popclose(val){
      this.logshow=val;
    },
    attrName(val){
      this.username=val;
      console.log(val);
    }
  },
  components:{
    'v-header':Header,
    'v-foot':Foot,
    'v-diglog':Diglog,
    'v-logform':Logform,
    'v-regform':Regform,
    'v-aboutform':Aboutform
  },
}
</script>

<style lang="stylus">
#app
  #container
    background #f3f3f3
</style>



