<template>
  <select>
    <option v-for="(item,index) in optionList" :value="item.value">{{item.label}}</option>
  </select>
</template>

<script>
    export default {
        props:['optionList']
    }
</script>

<style lang="stylus" scoped>
  select
    padding 5px 0
    padding-top 1px
    border-radius 5px
    font-size 14px
    option
      padding 0
</style>



