<template>
  <div id="analysis-wrapper">
    <div id="analysis">
      <h1>流量分析</h1>
      <p>{{anlsDesc}}</p>
      <div id="buy-num">
        <span>购买数量：</span>
        <v-changenum></v-changenum>
      </div>
      <div id="goods-type">
        <span>产品类型：</span>
        <v-option :optionList="typeoption"></v-option>
      </div>
      <div id="meijie">
        <span>产品版本：</span>
        <v-mulselect :goodType="goodType"></v-mulselect>
      </div>
      <div id="use-time">
        <span>有效时间：</span>
        <i>一年</i>
      </div>
      <div id="total-price">
        <span>总价：</span>
        <i>{{this.$store.state.totalPrice}}元</i>
      </div>
      <button id="buy-btn">立即购买</button>
    </div>
    <div id="product-desc">
      <h1>产品说明</h1>
      <p>网站访问统计分析报告的基础数据源于网站
        流量统计信息，但其价值远高于原始数据资料。
        专业的网站访问统计分析报告对网络营销的价值，
        正如专业的财务分析报告对企业经营策略的价值。
      </p>
      <h2>用户行为指标</h2>
      <p>用户行为指标主要反映用户是如何来到网站的、在网站上停留了多长时间、访问了哪些页面等，主要的统计指标包括：</p>
      <p>用户在网站的停留时间；</p>
      <p>用户来源网站（也叫“引导网站”）；</p>
      <p>用户所使用的搜索引擎及其关键词；</p>
      <p>在不同时段的用户访问量情况等。</p>
      <h2>浏览网站方式</h2>
      <p>用户上网设备类型</p>
      <p>用户浏览器的名称和版本</p>
      <p>访问者电脑分辨率显示模式</p>
      <p>用户所使用的操作系统名称和版本</p>
      <p>用户所在地理区域分布状况等</p>
    </div>
  </div>
</template>

<script>
  import changeNum from '../../components/changeNum'
  import mulSelect from '../../components/mulselect'
  import option from '../../components/option'
  export default {
    data(){
      return{
        anlsDesc:'是指在获得网站访问量基本数据的情况下对有关数据进行' +
          '统计、分析，从中发现用户访问网站的规律，并将这些规律与网络营' +
          '销策略等相结合，从而发现目前网络营销活动中可能存在的问题，并为' +
          '进一步修正或重新制定网络营销策略提供依据。当然这样的定义是站在' +
          '网络营销管理的角度来考虑的',
        goodType:[
          {
            label:'客户版',
            value:0,
            select:false
          },
          {
            label:'代理商版',
            value:1,
            select:false
          },
          {
            label:'专家版',
            value:2,
            select:false
          }
        ],
        typeoption:[
          {
            label:'初级版',
            value:0
          },
          {
            label:'中级版',
            value:1
          },
          {
            label:'高级版',
            value:2
          },
          {
            label:'专家版',
            value:3
          },
        ],
      }
    },
    components:{
      'v-changenum':changeNum,
      'v-mulselect':mulSelect,
      'v-option':option
    }
  }
</script>

<style lang="stylus" scoped>
  #analysis
    background #fff
    padding 20px
    margin-bottom 20px
    h1
      font-size 20px
    p
      line-height 20px
      color gray
      font-size 14px
      padding 10px 0
    #buy-num
      padding 10px 0
      display flex
      flex-flow row
      span
        width 100px
    #goods-type
      display flex
      line-height 25px
      padding 10px 0
      >span
        width 100px
        display inline-block
    #meijie
      padding 10px 0
      display flex
      flex-flow row
      span
        width 100px
    #use-time
      padding 10px 0
      span
        width 100px
        display inline-block
    #total-price
      padding 10px 0
      span
        width 100px
        display inline-block
      i
        color red
    #buy-btn
      margin-left 100px
      margin-top 10px
      padding 10px
      background #4fc08d
      color #fff
      border-radius 5px
      cursor pointer
  #product-desc
    background #fff
    padding 20px
    h1
      font-size 20px
      margin-bottom 10px
    p
      font-size 15px
      line-height 25px
      color #4c4c4c
    h2
      font-size 18px
      font-weight 600
      margin 20px 0 10px 0
      color #383333
</style>



