<template>
    <div id="slide" @mouseover="stopInv" @mouseout="runInv">
      <a :href="slideImg[nowIndex].href">
        <transition name="fade">
          <img v-show="isShow" :src="slideImg[nowIndex].src" alt="">
        </transition>
        <transition name="fade-old">
          <img v-show="!isShow" :src="slideImg[nowIndex].src" alt="">
        </transition>
      </a>
      <!--<transition-group tag="ul" id="img-wrapper" name="fade">-->
        <!--<li id="img-item" v-for="(item,index) in slideImg" :key="index" v-show="index===nowIndex">-->
          <!--<a :href="item.href">-->
            <!--<img :src="item.src" alt="">-->
          <!--</a>-->
        <!--</li>-->
      <!--</transition-group>-->
      <div id="button-wrapper">
        <ul id="imgSwitch">
          <li v-for="(item,index) in slideImg" @click="imgSwitch(index)" :class="{'select':nowIndex==index}"></li>
        </ul>
      </div>
      <div id="page-turn">
        <span @click="preTurn"><</span>
        <span @click="nextTurn">></span>
      </div>
    </div>
</template>

<script>
    export default {
        data(){
          return{
            nowIndex:0,
            isShow:true,
            slideImg:[
              {
                src:require('../assets/images/slideShow/pic1.jpg'),
                href:"www.baidu.com"
              },
              {
                src:require('../assets/images/slideShow/pic2.jpg'),
                href:"www.baidu.com"
              },
              {
                src:require('../assets/images/slideShow/pic3.jpg'),
                href:"www.baidu.com"
              },
              {
                src:require('../assets/images/slideShow/pic4.jpg'),
                href:"www.baidu.com"
              },
            ]
          }
        },
        methods:{
          imgSwitch(index){
            this.nowIndex=index;
          },
          preTurn(){
            if(this.nowIndex===0){
              return this.nowIndex=this.slideImg.length-1;
            }else{
              return this.nowIndex-=1;
            }
          },
          nextTurn(){
            if(this.nowIndex===this.slideImg.length-1){
              return this.nowIndex=0;
            }else{
              return this.nowIndex+=1;
            }
          },
          runInv(){
            this.invId=setInterval(()=>{
              this.nextTurn();
            },1800)
          },
          stopInv(){
            clearInterval(this.invId);
          },
        },
        mounted(){
          this.runInv()
        }
    }
</script>

<style lang="stylus" scoped>
  #slide
    width 830px
    position relative
    height 467px
    #img-wrapper
      position relative
      &.fade-enter-active, .fade-leave-active
        transition all 0.5s
      &.fade-enter, .fade-leave-to
        opacity 0
        transition translateX(830px)
      #img-item
        position absolute
        top 0
        >img
          width 830px
          height 467px
      .fade-enter-active {
        transition: all .5s;
      }
      .fade-enter {
        transform: translateX(830px);
      }
      .fade-old-leave-active {
        transition: all .5s;
        transform: translateX(-830px);
      }
    #button-wrapper
      position absolute
      bottom 0
      width 100%
      background rgba(0,0,0,0.5)
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      #imgSwitch
        display flex
        .select
          background #b3b3b3
        >li
          width 15px
          height 15px
          background #FFF
          margin-right 20px
          border-radius 50%
          cursor pointer
          &:last-of-type
            margin-right 0
    #page-turn
      position: absolute;
      height: 80px;
      top: 50%;
      margin-top: -25px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      padding 0 10px
      box-sizing border-box
      >span
        display block
        height 80px
        width 50px
        font-size 28px
        line-height 80px
        text-align center
        background rgba(0,0,0,0.5)
        color #fff
        border-radius 10px
        cursor pointer
</style>



