<template>
  <ul>
    <li v-for="(item,index) in goodType" :class="{'select':nowindex===index}" @click="select(index)">{{item.label}}</li>
  </ul>
</template>

<script>
    export default {
      props:['goodType'],
      data(){
        return{
          nowindex:0
        }
      },
      methods:{
        select(index){
          this.nowindex=index;
        }
      },

    }
</script>

<style lang="stylus" scoped>
  ul
    display flex
    li
      padding 0 5px
      border 1px solid #afafaf
      line-height 25px
      font-size 14px
      margin-right 15px
      border-radius 5px
      cursor pointer
      &:hover
        background #4fc08d
        color #fff
        border 1px solid #4fc08d
      &.select
        background #4fc08d
        color #fff
        border #4fc08d
</style>



