<template>
    <div id="forecast-wrapper">
      <div id="forecast">
        <h1>产品预测</h1>
        <p>{{fcstDesc}}</p>
        <div id="buy-num">
          <span>购买数量：</span>
          <v-changenum></v-changenum>
        </div>
        <div id="meijie">
          <span>媒介：</span>
          <v-mulselect :goodType="goodType"></v-mulselect>
        </div>
        <div id="use-time">
          <span>有效时间：</span>
          <i>一年</i>
        </div>
        <div id="total-price">
          <span>总价：</span>
          <i>{{this.$store.state.totalPrice}}元</i>
        </div>
        <button id="buy-btn">立即购买</button>
      </div>
      <div id="product-desc">
        <h1>产品说明</h1>
        <p>2020年的一天，在你驱车前往公司的路上，导航系统通过预测交通流量，
          会自动帮你选择一条最合适的交通路线；车内推荐系统会根据你的饮食习
          惯预测你可能会喜欢吃什么，并推荐沿途的早餐店；你的电子社交助理已
          经为你自动选择了你可能感兴趣的社交网信息；当车内系统预测到你驾车
          有些分心时，座椅会自动震动进行提醒…… 以上这些情景不是科幻大片独
          有的，它们有的已经或会在未来的某一天成为现实。而这一切所倚靠的就
          是预测分析技术。 大数据时代下，作为其核心，预测分析已在商业和社会
          中得到广泛应用。随着越来越多的数据被记录和整理，未来预测分析必定
          会成为所有领域的关键技术。 作为预测分析领域的专家，埃里克·西格尔
          博士深谙预测分析界已经实现和正在发生的事情、面临的问题和将来可能
          的前景。在《大数据预测》一书中，他结合预测分析的应用实例，对其进
          行了深入、细致且全面的解读。 关于预测分析，你想了解的全部， 你的
          生活以及这个世界会因为预测分析改变到什么程度，《大数据预测》都会
          告诉你。</p>
      </div>
    </div>
</template>

<script>
  import changeNum from '../../components/changeNum'
  import mulSelect from '../../components/mulselect'
    export default {
        data(){
          return{
            price:200,
            fcstDesc:'未来，大数据会变得越来越重要，其核心应用预测也会成为互联' +
              '网行业以及产业变革的重要力量，我们很有必要对数据预测及其分析方法进' +
              '行全面且深入的了解。在这一点上，' + '《大数据预测》是本很好的读物，' +
              '适合大数据所有相关行业的人阅读。',
            goodType:[
              {
                label:'纸质报告',
                value:0,
                select:false
              },
              {
                label:'pdf',
                value:1,
                select:false
              },
              {
                label:'页面报告',
                value:2,
                select:false
              },
              {
                label:'邮件',
                value:3,
                select:false
              }
            ],
          }
        },
        components:{
          'v-changenum':changeNum,
          'v-mulselect':mulSelect
        },
    }
</script>

<style lang="stylus" scoped>
  #forecast
    background #fff
    padding 20px
    margin-bottom 20px
    h1
      font-size 20px
    p
      line-height 20px
      color gray
      font-size 14px
      padding 10px 0
    #buy-num
      padding 10px 0
      display flex
      flex-flow row
      span
        width 100px
    #meijie
      padding 10px 0
      display flex
      flex-flow row
      span
        width 100px
    #use-time
      padding 10px 0
      span
        width 100px
        display inline-block
    #total-price
      padding 10px 0
      span
        width 100px
        display inline-block
      i
        color red
    #buy-btn
      margin-left 100px
      margin-top 10px
      padding 10px
      background #4fc08d
      color #fff
      border-radius 5px
      cursor pointer
  #product-desc
    background #fff
    padding 20px
    h1
      font-size 20px
    p
      padding 10px 0
      font-size 15px
      line-height 25px
      color #4c4c4c
</style>



