<template>
    <div>
      <i @click="min">-</i><span>{{this.$store.state.num}}</span><i @click="add">+</i>
    </div>
</template>

<script>
    export default {
      props:{
        'price':Number,
      },
      data(){
        return{
          num:1
        }
      },
      methods:{
        add(){
          this.$store.commit('addNum');
        },
        min(){
          this.$store.commit('minNum');
        }
      }
    }
</script>

<style lang="stylus" scoped>
  i
    width 25px
    height 25px
    display inline-block
    border 1px solid #c1c1c1
    text-align center
    line-height 25px
    cursor pointer
    &:hover
      background #4fc08d
      color #fff
      border 1px solid #4fc08d
  span
    width 40px
    height 25px
    display inline-block
    border 1px solid #c1c1c1
    text-align center
    line-height 25px
    border-left 0
    border-right 0
</style>



