// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import Vue from 'vue'
import App from './App'
import router from './router'
import './assets/css/reset.styl'
import Vuex from 'vuex'
Vue.use(Vuex)

Vue.config.productionTip = false

const store=new Vuex.Store({
  state:{
    num:1,
    price:200,
    totalPrice:200,
    userName:''
  },
  mutations:{
    addNum(state){
      state.num++;
      state.totalPrice = state.num*state.price;
    },
    minNum(state){
      if(state.num<=1){
        return
      }
      state.num--;
      state.totalPrice = state.num*state.price;
    },
    binduserName(state,val){
      state.userName=val;
    },
    resetUser(state){
      state.userName='';
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



