<template>
    <div id="header">
      <div id="header-wrapper">
        <router-link tag="div" id="logo" to="/">
          <img src="../assets/logo.png" alt="">
        </router-link>
        <ul id="login">
          <li :class="{'noborder':username==''}">{{username}}</li>
          <li v-show="username!==''" @click="out">退出</li>
          <li v-show="username==''" @click="logshow">登录</li>
          <li v-show="username==''" @click="regshow">注册</li>
          <li @click="aboutshow">关于</li>
        </ul>
      </div>
    </div>
</template>

<script>
    export default {
        data(){
          return{
            show:true,
          }
        },
        methods: {
          logshow() {
            this.$emit('log-show', this.show);
          },
          regshow() {
            this.$emit('reg-show', this.show);
          },
          aboutshow() {
            this.$emit('about-show', this.show);
          },
          out(){
            this.$store.commit('resetUser');
          }
        },
        computed:{
            username(){
              return this.$store.state.userName;
            }
        }
    }
</script>

<style lang="stylus" scoped>
  #header
    height 100px
    display flex
    justify-content center
    background #363636
    #header-wrapper
      width 80%
      display flex
      justify-content space-between
      align-items center
      #logo img
        width: 50px
        height: 50px
      #login
        display flex
        height: 20px;
        line-height: 20px;
        color #fff
        >li
          border-right 1px solid #fff
          width: 80px;
          text-align: center;
          cursor pointer
          &.noborder
            display none
          &:last-of-type
            border-right 0
</style>



