import Vue from 'vue'
import Router from 'vue-router'
import indexPage from '../pages/index'
import detailPage from '../pages/detail'
import countPage from '../pages/detailpage/count'
import forecastPage from '../pages/detailpage/forecast'
import analysisPage from '../pages/detailpage/analysis'
import publishPage from '../pages/detailpage/publish'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'indexPage',
      component: indexPage
    },
    {
      path: '/detail',
      name: 'detailPage',
      component: detailPage,
      children:[
        {
          path:'count',
          name:'countPage',
          component:countPage
        },
        {
          path:'forecast',
          name:'forecastPage',
          component:forecastPage
        },
        {
          path:'analysis',
          name:'analysisPage',
          component:analysisPage
        },
        {
          path:'publish',
          name:'publishPage',
          component:publishPage
        },
        {
          path:'/detail',
          redirect:'/detail/count'
        }
      ]
    },
  ]
})



