<template>
    <div id="popup">
      <div id="cover" @click="popClose"></div>
      <transition name="drop">
        <div id="form-wrapper">
          <button id="close" @click="popClose">X</button>
          <slot></slot>
        </div>
      </transition>
    </div>
</template>

<script>
    export default {
        data(){
          return{
            show:false
          }
        },
        methods:{
          popClose(){
            this.$emit('pop-close',this.show);
          }
        },

    }
</script>

<style lang="stylus" scoped>
  #popup
    width 100%
    height 100%
    position fixed
    top 0
    display flex
    justify-content center
    align-items center
    #cover
      width 100%
      height 100%
      position absolute
      background rgba(0,0,0,0.5)
    #form-wrapper
      width 500px
      padding 30px
      background #fff
      position relative
      border-radius 10px
      &.drop-enter-active, &.fade-leave-active {
        transition: all .5s;
      }
      &.drop-enter, &.fade-leave-to{
        transform translateY(-500px)
      }

      #close
        width 20px
        height 20px
        background #fff
        font-size 16px
        position absolute
        top 10px
        right 10px
        cursor pointer
</style>



