<template>
    <p>{{about}}</p>
</template>

<script>
    export default {
        data(){
          return{
            about:"本报告在调研数据的基础上，采用定性与定量相结合的方式深入分析了专车市场发" +
              "展的驱动因素与阻碍因素、专车市场背后的产业格局、" + "专车企业的竞争格局、用户" +
              "对专车市场的依赖程度、专车对其他交通工具运力的补充效应等，" + "通过这五个章节的研" +
              "究反映专车市场的发展态势和面临的问题。报告力求客观、深入、准确地反映中国专车市场" +
              "发展情况，为政府、企事业单位和社会各界提供决策依据。"
          }
        }
    }
</script>

<style scoped>
  p{
    line-height: 30px;
  }
</style>



