<template>
  <ul>
    <li :class="{'select':item.select}"
        @click="select(index,item)"
        v-for="(item,index) in goodType">
      {{item.label}}
    </li>
  </ul>
</template>

<script>
    export default {
        props:['goodType'],
        methods:{
          select(index,item){
            if(item.select==false){
              item.select=true;
              console.log("select:"+item.select);
            }else{
              item.select=false;
              console.log("select:"+item.select);
            }
          }
        }
    }
</script>

<style lang="stylus" scoped>
  ul
    display flex
    li
      padding 0 5px
      border 1px solid #afafaf
      line-height 25px
      font-size 14px
      margin-right 15px
      border-radius 5px
      cursor pointer
      &.select
        background #4fc08d
        color #fff
        border 1px solid #4fc08d
</style>



