<template>
  <div id="form">
    <div id="user-input">
      <label for="user">用户名:</label>
      <input v-model="userText" type="text" id="user" placeholder="请输入账号">
      <span id="errortext">{{usertextError.errorText}}</span>
    </div>
    <div id="mima-input">
      <label for="user">密码:</label>
      <input v-model="userPassword" type="password" id="mima" placeholder="请输入密码">
      <span id="errortext">{{passwordError.errorText}}</span>
    </div>
    <p id="login-error">{{loginError}}</p>
    <button id="log-btn" @click="onLogin">登录</button>
  </div>
</template>

<script>
  import Axios from 'axios'
  import Bus from '../../bus/built'
    export default {
        data(){
          return{
            userText:'',
            userPassword:'',
            loginError:'',
            popshow:true
          }
        },
        computed:{
          usertextError(){
            let errorText,status;
            if(!/@/g.test(this.userText)){
              status = false;
              errorText = "账号要包含@！";
            }else{
              status = true;
              errorText = " ";
            }
            if (!this.userFlag) {
              errorText = ''
              this.userFlag = true
            }
            return {status,errorText};
          },
          passwordError(){
            let errorText,status;
            if(!/^\w{1,6}$/g.test(this.userPassword)){
              status = false;
              errorText = "密码不是1~6位！";
            }else{
              status = true;
              errorText = " ";
            }
            if (!this.passwordFlag) {
              errorText = ''
              this.passwordFlag = true
            }
            return {status,errorText};
          }
        },
        methods:{
          onLogin(){
            if(!this.usertextError.status || !this.passwordError.status){
              this.loginError=" 账号密码错误！"
            }else{
              this.loginError=' ';
              //账号密码提交符合格式后请求后端页面验证
              console.log('登陆成功!');
              this.$store.commit('binduserName',this.userText);
              this.$emit('has-log',this.userText);
              this.popshow=false;
              this.$emit('pop-close',this.popshow);
            }
          }
        }
    }
</script>

<style lang="stylus" scoped>
  label
    display inline-block
    width 80px
    line-height 50px
  input
    padding 0 10px
    box-sizing border-box
    line-height 25px
  #errortext
    color red
    line-height 50px
    margin-left 10px
#login-error
  color red
  line-height 30px
  margin-left 80px
button
  padding 10px 20px
  margin-top 20px
  background #4fc08d
  border-radius 5px
  margin-left 80px
  color #fff
</style>



